/* Tic-Tac-Toe React Game on GitHub */
button {
    background-color: #0c80f3;
    border: 6px solid #0ff166;
    border-radius: 10px;
  }

.game-board {
width: 600px;
height: 600px;
margin: 0 auto;
background-color: #34495e;
color: rgb(204, 28, 28);
border: 6px solid #0b0d0f;
border-radius: 10px;
display: grid;
grid-template-rows: 1fr 1fr 1fr;
grid-template-columns: 1fr;
}

.grid-row {
border: 6px solid #739dc7;
border-radius: 6px;
font-family: Helvetica;
font-weight: bold;
font-size: 4em;
display: grid;
grid-template-rows: 1fr;
grid-template-columns: 1fr 1fr 1fr;
background-color: rgb(175, 70, 70);
}

.block {
background-color: rgb(219, 28, 28);
border: 6px solid green;
border-radius: 6px;
font-family: Helvetica;
font-weight: bold;
font-size: 20em;
}

/* Change player X or O text color */
.red {
color: red;
}

.white {
color: white;
}